'use strict';
(function () {
    angular
        .module('app.partner')
        .controller('Partner', ['$sce', 'partner', 'partnerFactory', 'comments', 'nbComments', Partner]);

    function Partner($sce, partner, partnerFactory, comments, nbComments) {
        /***************
         * DECLARATIONS
         **************/
        var vm = this;
        vm.partner = partner;
        vm.comments = comments;
        vm.nbComments = nbComments;

        _init();

        function _init() {
            partnerFactory.insertView(vm.partner.id).then(function() {
                angular.forEach(vm.comments, function (comment) {
                    comment.text = $sce.trustAsHtml(comment.text);
                });
            });
        }
    }
})();
