(function () {
    'use strict';

    angular
        .module('app')
        .service('commentsService', ['$http', commentsService]);

    function commentsService ($http) {
        function getCommentsByArticle (articleLabel) {
            return $http.get(host_url + 'api/comments/' + articleLabel)
                .then(function (response) {
                    return response.data;
                });
        }

        function getCommentsByPartner (partnerLabel) {
            return $http.get(host_url + 'api/comments/partner/' + partnerLabel)
                .then(function (response) {
                    return response.data;
                });
        }

        function sendComment (comment, articleId) {
            return $http({
                url: host_url + 'api/comments/' + articleId,
                method: 'POST',
                params: comment,
                headers: {
                    'Content-Type': 'application/json; charset=utf-8'
                }
            });
        }

        function sendPartnerComment (comment, partnerId) {
            return $http({
                url: host_url + 'api/comments/partner/' + partnerId,
                method: 'POST',
                params: comment,
                headers: {
                    'Content-Type': 'application/json; charset=utf-8'
                }
            });
        }

        return {
            getCommentsByArticle: getCommentsByArticle,
            getCommentsByPartner: getCommentsByPartner,
            sendComment: sendComment,
            sendPartnerComment: sendPartnerComment
        };
    }
})();
