'use strict';
(function () {
    angular
        .module('app.home')
        .controller('Home', ['$log', '$state', '$window', 'articleFactory', 'articles', 'pagination', Home]);

    function Home($log, $state, $window, articleFactory, articles, pagination) {

        $log.debug('[app.home] Loading Home controller');

        /***************
         * DECLARATIONS
         **************/

        var viewModel = this;
        viewModel.articles = articles;
        viewModel.pagination = pagination;

        viewModel.goToArticle = goToArticle;
        viewModel.goToPartner = goToPartner;
        viewModel.nextPage = nextPage;
        viewModel.previousPage = previousPage;

        /******************
         * IMPLEMENTATIONS
         *****************/

        function goToArticle(articleLabel) {
            var url = $state.href('articles', {'articleLabel': articleLabel});
            $window.open(url,'_blank');
            $window.scrollTo(0, 0);
        }

        function goToPartner(partnerLabel) {
            var url = $state.href('partner', {'partnerLabel': partnerLabel});
            $window.open(url,'_blank');
            $window.scrollTo(0, 0);
        }

        function nextPage() {
            var currentPage = viewModel.pagination.currentPage;
            articleFactory.fetchArticles(currentPage + 1).then(function (data) {
                viewModel.articles = data.articles;
                viewModel.pagination = data.pagination;
                $window.scrollTo(0, 0);
            });
        }

        function previousPage() {
            var currentPage = viewModel.pagination.currentPage;
            articleFactory.fetchArticles(currentPage - 1).then(function (data) {
                viewModel.articles = data.articles;
                viewModel.pagination = data.pagination;
                $window.scrollTo(0, 0);
            });
        }
    }
})();
