(function () {
    'use strict';

    angular
        .module('app.partner')
        .config(partnerRoute);

    function partnerRoute ($stateProvider) {
        $stateProvider
            .state('partner', {
                url: '/partner/:partnerLabel',
                views: {
                    '@': {
                        templateUrl: 'partner/views/partner.html',
                        controller: 'Partner',
                        controllerAs: 'viewModel',
                        title: 'partner',
                        resolve: {
                            partner: function ($stateParams, partnerFactory) {
                                return partnerFactory.fetchPartnerContent($stateParams.partnerLabel);
                            },
                            comments: function ($stateParams, commentsService) {
                                return commentsService.getCommentsByPartner($stateParams.partnerLabel).then(function(data) {
                                    return data.comments;
                                });
                            },
                            nbComments: function ($stateParams, commentsService) {
                                return commentsService.getCommentsByPartner($stateParams.partnerLabel).then(function(data) {
                                    return data.totalComments;
                                });
                            }
                        }
                    }
                }
            });
    }
})();
