(function () {
    'use strict';

    angular
        .module('app')
        .controller('CommentsController', ['$sanitize', 'commentsService', 'toastr', CommentsController]);

    function CommentsController ($sanitize, commentsService, toastr) {
        var vm = this;

        vm.comment = {
            pseudo: '',
            mail: '',
            url: '',
            text: ''
        };
        vm.sendComment = sendComment;

        function sendComment () {
            var comment = $sanitize(vm.comment.text);
            vm.comment.text = comment.replace(/\n/g, '<br />');
            if (vm.isRecipe) {
                commentsService.sendComment(vm.comment, vm.articleId).then(function () {
                    vm.comment = {
                        pseudo: '',
                        mail: '',
                        url: '',
                        text: ''
                    };
                    commentsService.getCommentsByArticle(vm.articleLabel).then(function (data) {
                        vm.comments = data.comments;
                        vm.nbComments = data.totalComments;
                    });
                    toastr.success('Commentaire envoyé !');
                });
            } else {
                commentsService.sendPartnerComment(vm.comment, vm.articleId).then(function () {
                    vm.comment = {
                        pseudo: '',
                        mail: '',
                        url: '',
                        text: ''
                    };
                    commentsService.getCommentsByPartner(vm.articleLabel).then(function (data) {
                        vm.comments = data.comments;
                        vm.nbComments = data.totalComments;
                    });
                    toastr.success('Commentaire envoyé !');
                });
            }
        }
    }
})();
