'use strict';
(function () {
    angular.module('app.partner')
        .factory('partnerFactory', ['$http', PartnerFactory]);

    function PartnerFactory($http) {

        /***************
         * DECLARATIONS
         **************/

        var factory = {
            //Variables

            //Functions
            fetchPartnerContent: fetchPartnerContent,
            insertView: insertView
        };

        return factory;

        /******************
         * IMPLEMENTATIONS
         *****************/

        function fetchPartnerContent (partnerLabel) {
            var req = {
                method: 'GET',
                url: host_url + 'api/partners/partner/' + partnerLabel,
                headers: {
                    'Content-Type': 'application/json; charset=utf-8'
                }
            };
            return $http(req).then(function (response) {
                return response.data.partner[0];
            });
        }

        function insertView (partnerId) {
            return $http.post(host_url + 'api/partners/partner/' + partnerId + '/view');
        }
    }
})();

