(function () {
    'use strict';

    angular
        .module('app.partners')
        .config(partnersRoute);

    function partnersRoute ($stateProvider) {
        $stateProvider
            .state('partners', {
                url: '/partners',
                views: {
                    '@': {
                        templateUrl: 'partners/views/partners.html',
                        title: 'partners'
                    }
                }
            });
    }
})();
